<template>
  <van-tabbar :value="active" class="tabMain">
    <van-tabbar-item v-for="(tabItem,index) in tabList" :key="index" :to="tabItem.route">
      <span>{{ tabItem.label }}</span>
      <img
        slot="icon"
        slot-scope="props"
        :src="props.active ? tabItem.icon.active : tabItem.icon.normal"
      />
    </van-tabbar-item>
  </van-tabbar>
</template>
<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
  data() {
    return {
      tabList: [
        {
          name: "activityIndex",
          label: "首页",
          icon: {
            normal: require("@/assets/img/activity/index.png"),
            active: require("@/assets/img/activity/index1.png")
          },
          num: 0,
          route: { name: "activityIndex" }
        },
        {
          name: "activityClassify",
          label: "分类",
          icon: {
            normal: require("@/assets/img/activity/classify.png"),
            active: require("@/assets/img/activity/classify1.png")
          },
          num: 0,
          route: { name: "activityClassify" }
        },
        {
          name: "activityUser",
          label: "我的",
          icon: {
            normal: require("@/assets/img/activity/user.png"),
            active: require("@/assets/img/activity/user1.png")
          },
          num: 0,
          route: { name: "activityUser" }
        }
      ]
    };
  },
  computed: {
    active() {
      return this.$route.meta.index;
    }
  }
};
</script>
<style lang="scss">
.tabMain {
  .van-tabbar-item__text {
    line-height: 16px;
  }
  .van-tabbar-item--active {
    color: $base-color;
  }
  .van-tabbar-item__icon {
    margin-bottom: 3px;
    img {
      height: 22px;
    }
  }
}
</style>

